@import url("https://fonts.googleapis.com/css?family=Josefin+Sans");
@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap");

h2 {
  /* font-family: "Josefin Sans", cursive; */
  font-family: "Dancing Script", cursive;
  text-align: center;
  background-color: rgb(110, 184, 147);
  margin-top: 0;
  padding: 15px;
}

.memory {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 40%;
  justify-content: center;
  margin: auto;
  box-shadow: 0 0 10px rgb(4, 110, 78);
}

.cell {
  width: 33%;
  height: 120px;
}

button:hover {
  background-color: rgb(194, 226, 210);
  border: none;
}
